import React, {Component} from 'react';
import Signin from '../components/Signin/';
import Layout from '../components/Layout/';

class Index extends Component {
  constructor() {
    super();
    this.state = {
      loggedIn: false,
    };
  }
  render() {
    return (
      <Layout>
        { this.state.loggedIn ? null : <Signin/> }
      </Layout>
    )
  }
}

export default Index;
